import React, { Component } from 'react'
import { Link } from 'react-router-dom'
import './index.css'
export default class Header extends Component {
    render() {
        return (
            <header className="container-fluid">
                <div className="container">
                    <nav className="navbar navbar-expand-lg navbar-light bg-ligh ">
                        <Link className="navbar-brand" to="/index">
                            <img src="./images/logo.png" height="40" alt="" />
                            L-blog
                        </Link>
                        <button className="navbar-toggler" type="button" data-toggle="collapse" data-target="#navbarNav"
                            aria-controls="navbarNav" aria-expanded="false" aria-label="Toggle navigation">
                            <span className="navbar-toggler-icon"></span>
                        </button>
                        <div className="collapse navbar-collapse" id="navbarNav">
                            <ul className="navbar-nav">
                                <li className="nav-item">
                                    <Link className="nav-link" to="/index">
                                        <span className="iconfont icon-shouye"></span>首页</Link>
                                </li>
                                <li className="nav-item">
                                    <Link className="nav-link" to="/article">
                                        <span className="iconfont icon-icon2"></span>文章</Link>
                                </li>
                                <li className="nav-item">
                                    <Link className="nav-link" to="/case">
                                        <span className="iconfont icon-icon1"></span>案例</Link>
                                </li>
                                <li className="nav-item">
                                    <Link className="nav-link" to="/">
                                        <span className="iconfont icon-icon"></span>心得</Link>
                                </li>
                            </ul>
                        </div>
                    </nav>
                </div>
            </header>
        )
    }
}
