import React, { Component } from 'react'
import '../../../node_modules/antd/dist/antd.css';
import './index.css'
import $ from 'jquery'

export default class Index extends Component {

    addAnimationFn(targetNodeIndex) {
        for (let i = 0; i < $('.item').length; i++) {
            if (i === targetNodeIndex) continue;
            $('.item').eq(i).addClass('sink')
        }
        $('.item').eq(targetNodeIndex).addClass('zFloat');
        $('header').eq(0).addClass('filter');
    }
    removeAnimationFn(targetNodeIndex) {
        for (let i = 0; i < $('.item').length; i++) {
            $('.item').eq(i).removeClass('sink')
        }
        $('.item').eq(targetNodeIndex).removeClass('zFloat');
        $('header').eq(0).removeClass('filter');
    }
    addFloat = (index) => {
        return () => {
            this.addAnimationFn(index)
        }
    }
    rmFloat = (index) => {
        return () => {
            this.removeAnimationFn(index)
        }
    }
    render() {
        return (
            <div className="container" id="case_c">
                <div className="item" onMouseEnter={this.addFloat(0)} onMouseLeave={this.rmFloat(0)}>
                    <div className="item_img">
                        <img src="./images/case/jd.jpg" alt="" />
                    </div>
                    <div className="item_content">
                        <div className="item_title">京东官网</div>
                        <div className="item_introduce">
                            <a target="_blank" rel="noopener noreferrer" href="https://lia0daye.github.io/lia0daye.resources.io/html/jd.html">这是我写的第一个案例，用了原生html，css，js编写，当时知识并不充足，需要在同学的帮助下，用来好长时间编写</a>
                        </div>
                    </div>
                </div>
                <div className="item" onMouseEnter={this.addFloat(1)} onMouseLeave={this.rmFloat(1)}>
                    <div className="item_img">
                        <img src="./images/case/mi.png" alt="" />
                    </div>
                    <div className="item_content">

                        <div className="item_title">小米官网</div>
                        <div className="item_introduce">
                            <a target="_blank" rel="noopener noreferrer" href="https://lia0daye.github.io/lia0daye.resources.io/html/mi.html">当时在学习过程中视频中有写到这个一个案例，就继续把它写下去，这个比较简单，只是用到一点点js，其他的用html和css就能编写完成</a>
                        </div>
                    </div>
                </div>
                <div className="item" onMouseEnter={this.addFloat(2)} onMouseLeave={this.rmFloat(2)}>
                    <div className="item_img">
                        <img src="./images/case/carousel-map.jpg" alt="" />
                    </div>
                    <div className="item_content">
                        <div className="item_title"> 渐隐轮播图</div>
                        <div className="item_introduce">
                            <a target="_blank" rel="noopener noreferrer" href="https://lia0daye.github.io/lia0daye.resources.io/html/carousel-map.html">唯一一个我会写的而且没有bug的轮播图,以前想写一个右往左滚动的轮播图,尝试了很多次,总是因为一些我无法解决的问题,不尽人意,最终放弃了</a>
                        </div>
                    </div>
                </div>
                <div className="item" onMouseEnter={this.addFloat(3)} onMouseLeave={this.rmFloat(3)}>
                    <div className="item_img">
                        <img src="./images/case/hamburg-button.jpg " alt="" />
                    </div>
                    <div className="item_content">
                        <div className="item_title">汉堡按钮</div>
                        <div className="item_introduce">
                            <a target="_blank" rel="noopener noreferrer" href="https://lia0daye.github.io/lia0daye.resources.io/html/hamburg-button.html">四个不同动画的汉堡按钮,在bilibili看到觉得不错就写了下来,效果还不错</a>
                        </div>
                    </div>
                </div>
                <div className="item" onMouseEnter={this.addFloat(4)} onMouseLeave={this.rmFloat(4)}>
                    <div className="item_img">
                        <img src="./images/case/Iron-man.jpg" alt="" />
                    </div>
                    <div className="item_content">
                        <div className="item_title">钢铁侠反应堆</div>
                        <div className="item_introduce">
                            <a target="_blank" rel="noopener noreferrer" href="https://lia0daye.github.io/lia0daye.resources.io/html/Iron-man.html">感觉非常炫酷,本来以为自己可以不用看视频自己写出来的,但是技不如人,还是看视频一步一步的编写出来,虽然是看着视频一边写的,也花了我一上午课,太菜了我</a>
                        </div>
                    </div>
                </div>
                <div className="item" onMouseEnter={this.addFloat(5)} onMouseLeave={this.rmFloat(5)}>
                    <div className="item_img">
                        <img src="./images/case/hamburg-button.jpg" alt="" />
                    </div>
                    <div className="item_content">
                        <div className="item_title">京东官网</div>
                        <div className="item_introduce">
                            <a target="_blank" rel="noopener noreferrer" href="/#">京东官网——这是我写的第一个案例，用了原生html，css，js编写，当时知识并不充足，需要在同学的帮助下，用来好长时间编写</a>
                        </div>
                    </div>
                </div>
            </div>
        )
    }
}
